}

@font-face {
  font-family: "RS";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/RobotoSlab Regular.eof") format("eof"), url("../fonts/RobotoSlab Regular.ttf") format("truetype"), url("../fonts/RobotoSlab Regular.woff") format("woff"), url("../fonts/RobotoSlab Regular.woff2") format("woff2"), url("../fonts/RobotoSlab Regular.svg#RS") format("svg");
